___CSS_LOADER_EXPORT___.push([module.id, `.settings-card[data-v-63750456]{width:100%;padding:12px;background:var(--settings-bg-glass);border:1px solid var(--border-strong);border-radius:16px;backdrop-filter:blur(10px);box-shadow:var(--settings-shadow-card),var(--settings-shadow-inset);display:flex;flex-direction:column;gap:10px;transition:background 0.3s ease,border-color 0.3s ease}.card-header[data-v-63750456]{display:flex;align-items:center;justify-content:space-between;padding:8px 10px;border-radius:10px;border:1px solid var(--border-soft);background:var(--chip-bg);color:var(--text-title);font-weight:800;font-size:13px;cursor:pointer;box-shadow:var(--settings-shadow-inset),var(--shadow-button);transition:background 0.3s ease,border-color 0.3s ease,color 0.3s ease,transform 0.2s ease,box-shadow 0.2s ease}.card-header[data-v-63750456]:hover{transform:translateY(-1px);box-shadow:var(--settings-shadow-inset),var(--shadow-button-hover)}.title[data-v-63750456]{pointer-events:none}.chev[data-v-63750456]{opacity:0.7}.content[data-v-63750456]{display:flex;flex-direction:column;gap:10px}.toolbar[data-v-63750456]{display:flex;align-items:center;gap:8px}.spacer[data-v-63750456]{flex:1}.mini-btn[data-v-63750456]{padding:6px 10px;border-radius:8px;border:1px solid var(--border-normal);background:var(--chip-bg);color:var(--text-normal);cursor:pointer;font-size:12px;font-weight:700;box-shadow:var(--settings-shadow-inset),var(--settings-shadow-minibtn);transition:background 0.3s ease,border-color 0.3s ease,color 0.3s ease,transform 0.2s ease,box-shadow 0.2s ease}.mini-btn[data-v-63750456]:hover{transform:translateY(-1px);box-shadow:var(--settings-shadow-inset),var(--settings-shadow-minibtn-hover)}.mini-btn.primary[data-v-63750456]{background:var(--settings-bg-btn-primary);border-color:var(--settings-border-btn-primary);color:var(--settings-text-primary-btn)}.mini-btn.subtle[data-v-63750456]{background:var(--settings-bg-btn-subtle);border-color:var(--settings-border-input)}.vars[data-v-63750456]{display:flex;flex-direction:column;gap:8px}.var-row[data-v-63750456]{display:grid;grid-template-columns:1fr minmax(120px,1.2fr) auto;align-items:center;gap:10px;padding:8px 10px;border:1px solid var(--border-soft);border-radius:10px;background:var(--settings-bg-var-row);transition:background 0.3s ease,border-color 0.3s ease}.var-key[data-v-63750456]{font-family:ui-monospace,SFMono-Regular,Menlo,Consolas,monospace;font-size:12px;color:var(--text-subtitle);overflow:hidden;text-overflow:ellipsis;white-space:nowrap;transition:color 0.3s ease}.var-editor[data-v-63750456]{display:flex;align-items:center;gap:8px}.ipt[data-v-63750456]{width:100%;padding:8px 10px;border-radius:8px;border:1px solid var(--settings-border-input);background:var(--bg-solid);font-size:12px;color:var(--settings-text-input);box-shadow:var(--settings-shadow-inset);transition:background 0.3s ease,border-color 0.3s ease,color 0.3s ease}.ta[data-v-63750456]{min-height:64px;font-family:ui-monospace,SFMono-Regular,Menlo,Consolas,monospace}.type-chip[data-v-63750456]{font-size:11px;color:var(--text-subtitle);background:var(--settings-bg-type-chip);border:1px solid var(--settings-border-type-chip);border-radius:999px;padding:4px 8px;white-space:nowrap;transition:background 0.3s ease,border-color 0.3s ease,color 0.3s ease}.switch[data-v-63750456]{position:relative;width:42px;height:24px;display:inline-block}.switch input[data-v-63750456]{opacity:0;width:0;height:0}.switch .track[data-v-63750456]{position:absolute;inset:0;border-radius:999px;background:var(--settings-bg-switch-track);box-shadow:var(--settings-shadow-inset);transition:background 0.2s ease}.switch .track[data-v-63750456]::after{content:'';position:absolute;top:3px;left:3px;width:18px;height:18px;border-radius:50%;background:var(--settings-bg-switch-handle);box-shadow:var(--settings-shadow-switch-handle);transition:transform 0.2s ease,background 0.3s ease}.switch input:checked+.track[data-v-63750456]{background:var(--settings-bg-switch-checked)}.switch input:checked+.track[data-v-63750456]::after{transform:translateX(18px)}.hint[data-v-63750456]{font-size:11px;color:var(--text-normal);transition:color 0.3s ease}.hint.ok[data-v-63750456]{color:var(--settings-text-hint-ok)}.hint.bad[data-v-63750456]{color:var(--settings-text-hint-bad)}.placeholder[data-v-63750456]{padding:10px;color:var(--text-normal);font-size:12px;border:1px dashed var(--border-dashed);border-radius:10px;background:var(--bg-placeholder);transition:background 0.3s ease,border-color 0.3s ease,color 0.3s ease}
`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/components/RightRailContent/EraSettingsPanel.vue"],"names":[],"mappings":"AA2PA,gCAEE,UAAW,CACX,YAAa,CACb,mCAAoC,CACpC,qCAAsC,CACtC,kBAAmB,CACnB,0BAA2B,CAC3B,mEAAqE,CACrE,YAAa,CACb,qBAAsB,CACtB,QAAS,CACT,sDAGF,CAGA,8BAEE,YAAa,CACb,kBAAmB,CACnB,6BAA8B,CAC9B,gBAAiB,CACjB,kBAAmB,CACnB,mCAAoC,CACpC,yBAA0B,CAC1B,uBAAwB,CACxB,eAAgB,CAChB,cAAe,CACf,cAAe,CACf,4DAA8D,CAC9D,+GAMF,CACA,oCACE,0BAA2B,CAC3B,kEACF,CACA,wBACE,mBACF,CACA,uBACE,WACF,CAGA,0BACE,YAAa,CACb,qBAAsB,CACtB,QACF,CAGA,0BACE,YAAa,CACb,kBAAmB,CACnB,OACF,CACA,yBACE,MACF,CAEA,2BAEE,gBAAiB,CACjB,iBAAkB,CAClB,qCAAsC,CACtC,yBAA0B,CAC1B,wBAAyB,CACzB,cAAe,CACf,cAAe,CACf,eAAgB,CAChB,sEAAwE,CACxE,+GAMF,CACA,iCACE,0BAA2B,CAC3B,4EACF,CACA,mCACE,yCAA0C,CAC1C,+CAAgD,CAChD,sCACF,CACA,kCACE,wCAAyC,CACzC,yCACF,CAGA,uBACE,YAAa,CACb,qBAAsB,CACtB,OACF,CAEA,0BAEE,YAAa,CACb,kDAAoD,CACpD,kBAAmB,CACnB,QAAS,CACT,gBAAiB,CACjB,mCAAoC,CACpC,kBAAmB,CACnB,qCAAsC,CACtC,sDAGF,CACA,0BACE,gEAAqE,CACrE,cAAe,CACf,0BAA2B,CAC3B,eAAgB,CAChB,sBAAuB,CACvB,kBAAmB,CACnB,0BACF,CACA,6BACE,YAAa,CACb,kBAAmB,CACnB,OACF,CAEA,sBAEE,UAAW,CACX,gBAAiB,CACjB,iBAAkB,CAClB,6CAA8C,CAC9C,0BAA2B,CAC3B,cAAe,CACf,gCAAiC,CACjC,uCAAwC,CACxC,sEAIF,CACA,qBACE,eAAgB,CAChB,gEACF,CAEA,4BAEE,cAAe,CACf,0BAA2B,CAC3B,uCAAwC,CACxC,iDAAkD,CAClD,mBAAoB,CACpB,eAAgB,CAChB,kBAAmB,CACnB,sEAIF,CAGA,yBACE,iBAAkB,CAClB,UAAW,CACX,WAAY,CACZ,oBACF,CACA,+BACE,SAAU,CACV,OAAQ,CACR,QACF,CACA,gCACE,iBAAkB,CAClB,OAAQ,CACR,mBAAoB,CACpB,0CAA2C,CAC3C,uCAAwC,CACxC,+BACF,CACA,uCACE,UAAW,CACX,iBAAkB,CAClB,OAAQ,CACR,QAAS,CACT,UAAW,CACX,WAAY,CACZ,iBAAkB,CAClB,2CAA4C,CAC5C,+CAAgD,CAChD,mDAGF,CACA,8CACE,4CACF,CACA,qDACE,0BACF,CAEA,uBACE,cAAe,CACf,wBAAyB,CACzB,0BACF,CACA,0BACE,kCACF,CACA,2BACE,mCACF,CAEA,8BACE,YAAa,CACb,wBAAyB,CACzB,cAAe,CACf,sCAAuC,CACvC,kBAAmB,CACnB,gCAAiC,CACjC,sEAIF","sourcesContent":["<template>\n  <!-- 外层卡片：与 ActionButtons 保持一致的玻璃卡风格，但默认收起 -->\n  <section class=\"settings-card\" role=\"region\" aria-label=\"ERA 设置\">\n    <!-- 设置卡片容器 -->\n    <!-- 标题行：可点击折叠/展开 -->\n    <button class=\"card-header\" :aria-expanded=\"isOpen\" @click=\"onToggle\">\n      <!-- 点击切换展开状态 -->\n      <span class=\"title\">ERA 设置</span>\n      <!-- 标题文字 -->\n      <span class=\"chev\" aria-hidden=\"true\">{{ isOpen ? '▾' : '▸' }}</span>\n      <!-- 展开箭头 -->\n    </button>\n\n    <!-- 内容体：默认收起；展开后显示变量列表与操作 -->\n    <div v-show=\"isOpen\" class=\"content\">\n      <!-- 展开内容容器 -->\n      <!-- 顶部操作区：刷新/保存/重置编辑 -->\n      <div class=\"toolbar\">\n        <!-- 工具条 -->\n        <button class=\"mini-btn\" title=\"重新读取脚本变量\" @click.stop=\"loadVars\">🔄 重新读取</button>\n        <!-- 读取按钮 -->\n        <div class=\"spacer\"></div>\n        <!-- 占位撑开 -->\n        <button class=\"mini-btn subtle\" title=\"丢弃未保存的修改\" @click.stop=\"discardEdits\">↩︎ 丢弃修改</button>\n        <!-- 丢弃编辑 -->\n        <button class=\"mini-btn primary\" title=\"保存修改到脚本变量\" @click.stop=\"saveEdits\">💾 保存修改</button>\n        <!-- 保存修改 -->\n      </div>\n\n      <!-- 变量列表：逐项渲染 -->\n      <div class=\"vars\">\n        <template v-if=\"rows.length > 0\">\n          <!-- 有变量时渲染列表 -->\n          <div v-for=\"row in rows\" :key=\"row.key\" class=\"var-row\">\n            <!-- 每个变量一行 -->\n            <label class=\"var-key\" :title=\"row.key\">{{ row.key }}</label>\n            <!-- 变量名 -->\n            <!-- 根据变量类型选择不同的编辑控件 -->\n            <div class=\"var-editor\">\n              <!-- 编辑器容器 -->\n              <!-- 布尔：勾选框 -->\n              <template v-if=\"row.type === 'boolean'\">\n                <label class=\"switch\">\n                  <!-- 自定义开关外观 -->\n                  <input v-model=\"edits[row.key]\" type=\"checkbox\" />\n                  <!-- 勾选值 -->\n                  <span class=\"track\"></span>\n                  <!-- 轨道 -->\n                </label>\n              </template>\n\n              <!-- 数字：number 输入 -->\n              <template v-else-if=\"row.type === 'number'\">\n                <input v-model.number=\"edits[row.key]\" type=\"number\" class=\"ipt\" :placeholder=\"String(row.value)\" />\n                <!-- 数字输入 -->\n              </template>\n\n              <!-- 字符串：text 输入 -->\n              <template v-else-if=\"row.type === 'string'\">\n                <input v-model=\"edits[row.key]\" type=\"text\" class=\"ipt\" :placeholder=\"String(row.value)\" />\n                <!-- 文本输入 -->\n              </template>\n\n              <!-- 其他（对象/数组/null/未知）：JSON 文本域 -->\n              <template v-else>\n                <textarea\n                  v-model=\"jsonBuffers[row.key]\"\n                  class=\"ipt ta\"\n                  :placeholder=\"safeStringify(row.value)\"\n                  @input=\"markJsonTouched(row.key)\"\n                ></textarea>\n                <!-- JSON 文本域 -->\n                <span class=\"hint\" :class=\"{ ok: jsonState[row.key]?.ok, bad: jsonState[row.key]?.bad }\">{{\n                  jsonState[row.key]?.msg ?? '以 JSON 格式编辑此值'\n                }}</span>\n                <!-- JSON 校验提示 -->\n              </template>\n            </div>\n            <!-- 类型徽标 -->\n            <span class=\"type-chip\">{{ humanType(row.type) }}</span>\n            <!-- 类型展示 -->\n          </div>\n        </template>\n\n        <template v-else>\n          <!-- 没有变量时的占位 -->\n          <div class=\"placeholder\">\n            <p>未读取到任何脚本变量。</p>\n            <!-- 文本提示 -->\n            <p class=\"dim\">\n              请先确保在 <code>app_ready</code> 或初始化阶段通过\n              <code>initializeExternalSettings()</code> 写入默认变量。\n            </p>\n            <!-- 辅助说明 -->\n          </div>\n        </template>\n      </div>\n    </div>\n  </section>\n</template>\n\n<script setup lang=\"ts\">\n/* 块头：逻辑说明\n   - 默认收起，展开时读取脚本域变量；\n   - 支持 boolean/number/string/json 四类编辑；\n   - 保存时按原类型回写到脚本域。\n*/\nimport { reactive, ref } from 'vue'; // 中文注释：Vue 响应式 API\nimport { z } from 'zod';\nimport { SettingsSchema } from '../../../utils/constants';\nimport { getScriptSettings, updateScriptSettings } from '../../../utils/era_data';\nimport { Logger } from '../../../utils/log'; // 中文注释：项目内日志工具\n\nconst logger = new Logger(); // 中文注释：实例化日志\n\n// === 折叠状态 ===\nconst isOpen = ref(false); // 中文注释：是否展开\n\n// === 原始数据与编辑缓存 ===\ntype Row = { key: string; value: any; type: 'boolean' | 'number' | 'string' | 'json' }; // 中文注释：一行变量的结构\nconst rows = ref<Row[]>([]); // 中文注释：可渲染的变量行\nconst edits = reactive<Record<string, any>>({}); // 中文注释：针对简单类型的编辑缓存（布尔/数字/字符串）\nconst jsonBuffers = reactive<Record<string, string>>({}); // 中文注释：针对 JSON 类型的文本缓存\nconst jsonState = reactive<Record<string, { ok?: boolean; bad?: boolean; msg?: string }>>({}); // 中文注释：JSON 校验状态\n\n// === 工具：类型判定与人类可读名 ===\nfunction detectType(v: any): Row['type'] {\n  // 中文注释：检测变量类型\n  if (typeof v === 'boolean') return 'boolean'; // 中文注释：布尔\n  if (typeof v === 'number' && Number.isFinite(v)) return 'number'; // 中文注释：有限数\n  if (typeof v === 'string') return 'string'; // 中文注释：字符串\n  return 'json'; // 中文注释：其他一律归为 json（对象/数组/null/特殊数等）\n}\nfunction humanType(t: Row['type']): string {\n  // 中文注释：类型中文名\n  return t === 'boolean' ? '布尔' : t === 'number' ? '数字' : t === 'string' ? '字符串' : 'JSON'; // 中文注释：映射\n}\nfunction safeStringify(v: any): string {\n  // 中文注释：安全 JSON 序列化\n  try {\n    return JSON.stringify(v, null, 2);\n  } catch {\n    return String(v);\n  } // 中文注释：失败则转字符串\n}\n\n// === 加载变量列表 ===\nfunction loadVars() {\n  try {\n    logger.debug('loadVars', '尝试读取脚本设置...');\n    const obj = getScriptSettings();\n    logger.debug('loadVars', '成功读取脚本设置:', obj);\n\n    if (!obj || Object.keys(obj).length === 0) {\n      logger.warn('loadVars', '设置对象为空，UI 将不会被填充。');\n      rows.value = [];\n      return;\n    }\n\n    // 清空旧缓存\n    rows.value = [];\n    for (const k of Object.keys(edits)) delete edits[k];\n    for (const k of Object.keys(jsonBuffers)) delete jsonBuffers[k];\n    for (const k of Object.keys(jsonState)) delete jsonState[k];\n\n    // 构建可编辑行\n    Object.entries(obj)\n      .filter(([key]) => key !== '开启悬浮球')\n      .forEach(([key, value]) => {\n        const t = detectType(value);\n        rows.value.push({ key, value, type: t });\n        if (t === 'json') {\n          jsonBuffers[key] = safeStringify(value);\n        } else {\n          edits[key] = value;\n        }\n      });\n  } catch (e) {\n    logger.error('loadVars', '读取脚本变量失败:', e);\n    alert('读取 ERA 设置失败，请检查浏览器控制台获取详细信息。');\n  }\n}\n\n// === 展开折叠 ===\nfunction onToggle() {\n  // 中文注释：切换展开\n  isOpen.value = !isOpen.value; // 中文注释：翻转状态\n  if (isOpen.value && rows.value.length === 0) loadVars(); // 中文注释：首次展开时读取数据\n}\n\n// === JSON 文本域编辑标记 + 校验 ===\nfunction markJsonTouched(key: string) {\n  // 中文注释：标记 JSON 文本改动并校验\n  const txt = jsonBuffers[key] ?? ''; // 中文注释：读取文本\n  if (txt.trim() === '') {\n    // 中文注释：空文本提示\n    jsonState[key] = { bad: true, ok: false, msg: '不能为空（若要设为 null 请写 null）' }; // 中文注释：空提示\n    return; // 中文注释：返回\n  }\n  try {\n    JSON.parse(txt);\n    jsonState[key] = { ok: true, bad: false, msg: 'JSON 格式有效' };\n  } catch (e: any) {\n    // 中文注释：校验通过\n    jsonState[key] = { bad: true, ok: false, msg: `JSON 格式错误：${e?.message ?? ''}` };\n  } // 中文注释：校验失败\n}\n\n// === 丢弃未保存的编辑 ===\nfunction discardEdits() {\n  // 中文注释：丢弃编辑\n  loadVars(); // 中文注释：重新读取覆盖本地改动\n}\n\n// === 保存修改 ===\nasync function saveEdits() {\n  // 中文注释：保存到脚本域\n  try {\n    await updateScriptSettings(currentSettings => {\n      const next: z.infer<typeof SettingsSchema> = { ...currentSettings };\n\n      // 合并简单类型的改动（布尔/数字/字符串）\n      Object.entries(edits).forEach(([k, v]) => {\n        const key = k as keyof z.infer<typeof SettingsSchema>;\n        (next as any)[key] = v;\n      });\n\n      // 合并 JSON 文本改动（需先通过校验）\n      Object.entries(jsonBuffers).forEach(([k, txt]) => {\n        if (txt != null) {\n          if (jsonState[k]?.bad) throw new Error(`键 ${k} 的 JSON 格式不正确`);\n          const key = k as keyof z.infer<typeof SettingsSchema>;\n          (next as any)[key] = JSON.parse(txt);\n        }\n      });\n\n      return next;\n    });\n\n    logger.log('saveEdits', '脚本变量已保存');\n    window.dispatchEvent(new CustomEvent('era-settings-updated'));\n    loadVars();\n  } catch (e) {\n    logger.error('saveEdits', '保存失败：', e);\n    alert(`保存失败：${(e as any)?.message ?? e}`);\n  }\n}\n</script>\n\n<style scoped>\n/* === 外层卡片容器（与 ActionButtons 保持同系外观） === */\n.settings-card {\n  /* 玻璃卡容器 */\n  width: 100%; /* 占满侧栏宽度 */\n  padding: 12px; /* 内边距 */\n  background: var(--settings-bg-glass);\n  border: 1px solid var(--border-strong);\n  border-radius: 16px; /* 圆角 */\n  backdrop-filter: blur(10px); /* 毛玻璃 */\n  box-shadow: var(--settings-shadow-card), var(--settings-shadow-inset);\n  display: flex; /* 纵向布局 */\n  flex-direction: column; /* 垂直排布 */\n  gap: 10px; /* 间距 */\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n\n/* 标题按钮：收起/展开控制 */\n.card-header {\n  /* 标题行按钮 */\n  display: flex; /* 横向布局 */\n  align-items: center; /* 垂直居中 */\n  justify-content: space-between; /* 两端对齐 */\n  padding: 8px 10px; /* 内边距 */\n  border-radius: 10px; /* 圆角 */\n  border: 1px solid var(--border-soft);\n  background: var(--chip-bg);\n  color: var(--text-title);\n  font-weight: 800; /* 加粗 */\n  font-size: 13px; /* 字号 */\n  cursor: pointer; /* 可点 */\n  box-shadow: var(--settings-shadow-inset), var(--shadow-button);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.card-header:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--settings-shadow-inset), var(--shadow-button-hover);\n} /* 悬停态 */\n.title {\n  pointer-events: none;\n} /* 避免子元素截获点击 */\n.chev {\n  opacity: 0.7;\n} /* 箭头淡化 */\n\n/* 内容体 */\n.content {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n} /* 内容容器 */\n\n/* 工具条 */\n.toolbar {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n} /* 工具条布局 */\n.spacer {\n  flex: 1;\n} /* 右对齐按钮用 */\n\n.mini-btn {\n  /* 通用小按钮 */\n  padding: 6px 10px; /* 内边距 */\n  border-radius: 8px; /* 圆角 */\n  border: 1px solid var(--border-normal);\n  background: var(--chip-bg);\n  color: var(--text-normal);\n  cursor: pointer; /* 可点击 */\n  font-size: 12px; /* 字号 */\n  font-weight: 700; /* 加粗 */\n  box-shadow: var(--settings-shadow-inset), var(--settings-shadow-minibtn);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.mini-btn:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--settings-shadow-inset), var(--settings-shadow-minibtn-hover);\n} /* 悬停 */\n.mini-btn.primary {\n  background: var(--settings-bg-btn-primary);\n  border-color: var(--settings-border-btn-primary);\n  color: var(--settings-text-primary-btn);\n} /* 主要 */\n.mini-btn.subtle {\n  background: var(--settings-bg-btn-subtle);\n  border-color: var(--settings-border-input);\n} /* 次要 */\n\n/* 变量列表 */\n.vars {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n} /* 列表容器 */\n\n.var-row {\n  /* 一行变量 */\n  display: grid; /* 网格布局 */\n  grid-template-columns: 1fr minmax(120px, 1.2fr) auto; /* 三列：键 / 编辑器 / 类型 */\n  align-items: center; /* 垂直居中 */\n  gap: 10px; /* 列间距 */\n  padding: 8px 10px; /* 内边距 */\n  border: 1px solid var(--border-soft);\n  border-radius: 10px; /* 圆角 */\n  background: var(--settings-bg-var-row);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.var-key {\n  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;\n  font-size: 12px;\n  color: var(--text-subtitle);\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  transition: color 0.3s ease;\n} /* 键名样式 */\n.var-editor {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n} /* 编辑器容器 */\n\n.ipt {\n  /* 通用输入框 */\n  width: 100%; /* 拉满可用宽度 */\n  padding: 8px 10px; /* 内边距 */\n  border-radius: 8px; /* 圆角 */\n  border: 1px solid var(--settings-border-input);\n  background: var(--bg-solid);\n  font-size: 12px; /* 字号 */\n  color: var(--settings-text-input);\n  box-shadow: var(--settings-shadow-inset);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n.ta {\n  min-height: 64px;\n  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;\n} /* JSON 文本域 */\n\n.type-chip {\n  /* 类型标签 */\n  font-size: 11px; /* 字号 */\n  color: var(--text-subtitle);\n  background: var(--settings-bg-type-chip);\n  border: 1px solid var(--settings-border-type-chip);\n  border-radius: 999px; /* 胶囊 */\n  padding: 4px 8px; /* 内边距 */\n  white-space: nowrap; /* 不换行 */\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n\n/* 自定义开关（布尔） */\n.switch {\n  position: relative;\n  width: 42px;\n  height: 24px;\n  display: inline-block;\n} /* 开关容器 */\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n} /* 隐藏原生框 */\n.switch .track {\n  position: absolute;\n  inset: 0;\n  border-radius: 999px;\n  background: var(--settings-bg-switch-track);\n  box-shadow: var(--settings-shadow-inset);\n  transition: background 0.2s ease;\n} /* 轨道 */\n.switch .track::after {\n  content: '';\n  position: absolute;\n  top: 3px;\n  left: 3px;\n  width: 18px;\n  height: 18px;\n  border-radius: 50%;\n  background: var(--settings-bg-switch-handle);\n  box-shadow: var(--settings-shadow-switch-handle);\n  transition:\n    transform 0.2s ease,\n    background 0.3s ease;\n} /* 拖头 */\n.switch input:checked + .track {\n  background: var(--settings-bg-switch-checked);\n} /* 选中轨道色 */\n.switch input:checked + .track::after {\n  transform: translateX(18px);\n} /* 拖头右移 */\n\n.hint {\n  font-size: 11px;\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n} /* JSON 提示 */\n.hint.ok {\n  color: var(--settings-text-hint-ok);\n} /* 通过提示色 */\n.hint.bad {\n  color: var(--settings-text-hint-bad);\n} /* 错误提示色 */\n\n.placeholder {\n  padding: 10px;\n  color: var(--text-normal);\n  font-size: 12px;\n  border: 1px dashed var(--border-dashed);\n  border-radius: 10px;\n  background: var(--bg-placeholder);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n} /* 空状态 */\n</style>\n"],"sourceRoot":""}]);
