`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/template/TabSwitch.vue"],"names":[],"mappings":"AA+DA,uBACE,mCAAoC,CACpC,kBAAmB,CACnB,eAAgB,CAChB,8BAA+B,CAC/B,sDAGF,CACA,0BACE,YAAa,CACb,OAAQ,CACR,WAAY,CACZ,0CAA2C,CAC3C,iCAAkC,CAClC,sDAGF,CACA,0BACE,gBAAiB,CACjB,eAAgB,CAChB,cAAe,CACf,wBAAyB,CACzB,0BAA2B,CAC3B,6CAA8C,CAC9C,kBAAmB,CACnB,cAAe,CACf,2FAKF,CACA,iCACE,gCAAiC,CACjC,+CAAgD,CAChD,gEACF,CACA,8BACE,iBAAkB,CAClB,gCAAiC,CACjC,+BACF","sourcesContent":["<template>\n  <section class=\"tabs\">\n    <!-- 页签行 -->\n    <div class=\"tab-bar\" role=\"tablist\">\n      <button\n        v-for=\"t in tabs\"\n        :key=\"t.key\"\n        class=\"tab-btn\"\n        :class=\"{ active: t.key === innerActive }\"\n        role=\"tab\"\n        @click=\"setActive(t.key)\"\n      >\n        {{ t.label }}\n      </button>\n    </div>\n\n    <!-- 内容区：根据活动键渲染对应插槽 -->\n    <div class=\"tab-content\">\n      <div v-if=\"innerActive === 'pure'\">\n        <slot name=\"pure\" />\n        <!-- 纯净状态数据内容 -->\n      </div>\n      <div v-else>\n        <slot name=\"full\" />\n        <!-- 完整状态数据内容 -->\n      </div>\n    </div>\n  </section>\n</template>\n\n<script setup lang=\"ts\">\nimport { onMounted, ref, watch } from 'vue'; // 引入响应式工具\nimport { Logger } from '../../utils/log';\n\ntype TabItem = { key: 'pure' | 'full'; label: string }; // Tab 项类型\n\nconst logger = new Logger();\nconst props = defineProps<{ tabs: TabItem[]; active?: 'pure' | 'full' }>(); // 输入 tabs 与可选 active\nconst emit = defineEmits<{ 'update:active': ['pure' | 'full'] }>(); // v-model:active\n\nconst innerActive = ref<'pure' | 'full'>(props.active ?? 'pure'); // 内部活动键\n\nwatch(\n  () => props.active,\n  v => {\n    if (v) {\n      logger.debug('watch:active', `外部同步 active tab 为: ${v}`);\n      innerActive.value = v;\n    }\n  },\n); // 外部变更时同步\nfunction setActive(k: 'pure' | 'full') {\n  logger.log('setActive', `用户点击，切换 tab 到: ${k}`);\n  innerActive.value = k;\n  emit('update:active', k);\n} // 切换并抛出\n\nonMounted(() => {\n  logger.log('onMounted', '组件已挂载', { props });\n});\n</script>\n\n<style scoped>\n.tabs {\n  border: 1px solid var(--border-soft);\n  border-radius: 12px;\n  overflow: hidden;\n  background: var(--accordion-bg);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.tab-bar {\n  display: flex;\n  gap: 8px;\n  padding: 8px;\n  border-bottom: 1px solid var(--border-soft);\n  background: var(--mk-pill-null-bg);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.tab-btn {\n  padding: 8px 12px;\n  font-weight: 800;\n  font-size: 13px;\n  color: var(--text-normal);\n  background: var(--bg-solid);\n  border: 1px solid var(--settings-border-input);\n  border-radius: 10px;\n  cursor: pointer;\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    box-shadow 0.3s ease;\n}\n.tab-btn.active {\n  color: var(--settings-text-input);\n  border-color: var(--settings-border-btn-primary);\n  box-shadow: var(--settings-shadow-inset), var(--tab-active-shadow);\n}\n.tab-content {\n  padding: 10px 12px;\n  background: var(--tab-content-bg);\n  transition: background 0.3s ease;\n}\n</style>\n"],"sourceRoot":""}]);
