`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/components/EraPanel.vue"],"names":[],"mappings":"AAmEA,4BACE,cAAe,CACf,eAAgB,CAChB,sBAAwB,CACxB,YAAa,CACb,qBAAsB,CACtB,+BAAgC,CAChC,qCAAsC,CACtC,0BAA2B,CAC3B,kBAAmB,CACnB,kDAAoD,CACpD,eAAgB,CAChB,gEAAqE,CACrE,sDAGF,CAEA,4BACE,YAAa,CACb,kBAAmB,CACnB,6BAA8B,CAC9B,iBAAkB,CAClB,0CAA2C,CAC3C,+BAAgC,CAChC,sDAGF,CACA,8BACE,cAAe,CACf,eAAgB,CAChB,oBAAqB,CACrB,uBAAwB,CACxB,0BACF,CACA,4BACE,UAAW,CACX,WAAY,CACZ,gBAAiB,CACjB,iBAAkB,CAClB,iBAAkB,CAClB,qCAAsC,CACtC,0BAA2B,CAC3B,cAAe,CACf,cAAe,CACf,wBAAyB,CACzB,+BAAgC,CAChC,+GAMF,CACA,kCACE,0BAA2B,CAC3B,qCACF,CAEA,6BACE,SAAU,CACV,eAAgB,CAChB,WAAY,CACZ,YAAa,CACb,qBACF,CAEA,wCACE,sBAAuB,CACvB,eAAgB,CAChB,WACF,CAGA,4CACE,eACF,CAEA,wBACE,WAAY,CACZ,YAAa,CACb,kBAAmB,CACnB,wBAAyB,CACzB,cAAe,CACf,sCAAuC,CACvC,kBAAmB,CACnB,0BAA2B,CAC3B,sEAIF","sourcesContent":["<template>\n  <div class=\"era-panel\">\n    <header class=\"panel-top\">\n      <h3 class=\"panel-title\">ERA 数据面板</h3>\n      <button class=\"btn-close\" aria-label=\"关闭\" @click=\"requestSwitchView('FloatingBall')\">×</button>\n    </header>\n    <div class=\"panel-body\">\n      <div class=\"panel-content-wrapper\">\n        <template v-if=\"data\">\n          <EraAccordion title=\"最新消息元数据\" :default-open=\"false\">\n            <MetaHeader :mk=\"data.mk\" :message-id=\"data.message_id\" />\n          </EraAccordion>\n\n          <OperationDetails :data=\"data\" />\n\n          <TabSwitch v-model:active=\"activeTab\" :tabs=\"tabs\">\n            <template #pure>\n              <PrettyJsonViewer :value=\"data.statWithoutMeta\" :default-collapsed=\"true\" :max-depth=\"Infinity\" />\n            </template>\n            <template #full>\n              <PrettyJsonViewer :value=\"data.stat\" :default-collapsed=\"true\" :max-depth=\"Infinity\" />\n            </template>\n          </TabSwitch>\n        </template>\n        <template v-else>\n          <div class=\"empty\">等待 era:writeDone 事件数据…</div>\n        </template>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport { Logger } from '../../utils/log';\nimport EraAccordion from '../template/EraAccordion.vue';\nimport MetaHeader from './EraPanelContent/MetaHeader.vue';\nimport TabSwitch from '../template/TabSwitch.vue';\nimport OperationDetails from './EraPanelContent/OperationDetails.vue';\nimport PrettyJsonViewer from '../template/PrettyJsonViewer.vue';\nimport type { WriteDonePayload } from '../../utils/constants';\n\ntype TabItem = { key: 'pure' | 'full'; label: string };\n\nconst logger = new Logger();\n\ndefineProps<{\n  data: WriteDonePayload | null;\n}>();\n\nconst requestSwitchView = (viewName: 'FloatingBall' | 'ExpandedView') => {\n  logger.debug('requestSwitchView', `请求切换视图到: ${viewName}`);\n  if ((window as any).eraUiSwitchView) {\n    (window as any).eraUiSwitchView(viewName);\n  } else {\n    logger.warn('requestSwitchView', '全局切换函数 eraUiSwitchView 未找到');\n  }\n};\n\nconst tabs: TabItem[] = [\n  { key: 'pure', label: '纯净状态数据' },\n  { key: 'full', label: '完整状态数据' },\n];\nconst activeTab = ref<'pure' | 'full'>('pure');\n</script>\n\n<style scoped>\n.era-panel {\n  flex: 2 1 600px;\n  min-width: 320px;\n  height: min(680px, 86vh);\n  display: flex;\n  flex-direction: column;\n  background: var(--bg-blur-heavy);\n  border: 1px solid var(--border-strong);\n  backdrop-filter: blur(10px);\n  border-radius: 16px;\n  box-shadow: var(--shadow-panel), var(--shadow-inset);\n  overflow: hidden;\n  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n\n.panel-top {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 14px 16px;\n  border-bottom: 1px solid var(--border-soft);\n  background: var(--bg-blur-light);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n.panel-title {\n  font-size: 16px;\n  font-weight: 800;\n  letter-spacing: 0.5px;\n  color: var(--text-title);\n  transition: color 0.3s ease;\n}\n.btn-close {\n  width: 32px;\n  height: 32px;\n  line-height: 30px;\n  text-align: center;\n  border-radius: 8px;\n  border: 1px solid var(--border-normal);\n  background: var(--bg-solid);\n  cursor: pointer;\n  font-size: 18px;\n  color: var(--text-normal);\n  box-shadow: var(--shadow-button);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease,\n    transform 0.2s ease,\n    box-shadow 0.2s ease;\n}\n.btn-close:hover {\n  transform: translateY(-1px);\n  box-shadow: var(--shadow-button-hover);\n}\n\n.panel-body {\n  padding: 0;\n  overflow: hidden;\n  flex-grow: 1;\n  display: flex; /* 确保 wrapper 能撑满 */\n  flex-direction: column;\n}\n\n.panel-content-wrapper {\n  padding: 14px 14px 16px;\n  overflow-y: auto; /* 内部滚动 */\n  flex-grow: 1;\n}\n\n/* 用 margin 替代 gap */\n.panel-content-wrapper > * + * {\n  margin-top: 12px;\n}\n\n.empty {\n  height: 100%; /* 撑满父容器 */\n  display: grid;\n  place-items: center;\n  color: var(--text-normal);\n  font-size: 14px;\n  border: 1px dashed var(--border-dashed);\n  border-radius: 12px;\n  background: var(--bg-empty);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n</style>\n"],"sourceRoot":""}]);
