`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/template/PrettyJsonViewer.vue"],"names":[],"mappings":"AAkIA,4BACE,cAAe,CACf,gCAAiC,CACjC,0BACF,CACA,4BACE,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,OAAQ,CACR,eAAgB,CAChB,iBAAkB,CAClB,+BACF,CACA,kCACE,+BACF,CACA,gCACE,4CAA6C,CAC7C,iCACF,CAEA,sBACE,2BAA4B,CAC5B,eAAgB,CAChB,0BACF,CACA,wBACE,wBAAyB,CACzB,0BACF,CACA,wBACE,wBAAyB,CACzB,0BACF,CAEA,6BACE,4BACF,CACA,6BACE,4BACF,CACA,8BACE,6BACF,CACA,2BACE,0BACF,CACA,gCACE,0BACF,CAGA,4BAEE,aAAc,CACd,oBAAqB,CACrB,YACF,CAEA,gCAEE,aAAc,CACd,aACF","sourcesContent":["<template>\n  <div class=\"json-root\">\n    <div class=\"json-line\">\n      <span class=\"brace\">{{ rootOpen }}</span>\n      <!-- 根开括号：对象{ / 数组[ -->\n    </div>\n\n    <div class=\"json-children\">\n      <template v-if=\"isPlainObjectOrArray\">\n        <JsonNode\n          v-for=\"(v, k) in value\"\n          :key=\"String(k)\"\n          :k=\"String(k)\"\n          :v=\"v\"\n          :path=\"String(k)\"\n          :level=\"1\"\n          :default-collapsed=\"defaultCollapsed\"\n          :max-depth=\"maxDepth\"\n        />\n      </template>\n\n      <template v-else>\n        <div class=\"json-line\" :style=\"{ paddingLeft: 1 * 14 + 'px' }\">\n          <span class=\"key\">value</span><span class=\"colon\">:</span>\n          <span class=\"val\" :class=\"primitiveType\">{{ primitiveText }}</span>\n        </div>\n      </template>\n    </div>\n\n    <div class=\"json-line\">\n      <span class=\"brace\">{{ rootClose }}</span>\n      <!-- 根闭括号：对象} / 数组] -->\n    </div>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { computed } from 'vue'; // 计算属性工具\nimport { Logger } from '../../utils/log';\nimport JsonNode from './JsonNode.vue'; // ✅ 改为导入独立 SFC 的递归节点组件\n\nconst logger = new Logger();\n\nconst props = withDefaults(\n  defineProps<{\n    value: any; // 要展示的 JSON 值\n    defaultCollapsed?: boolean; // 默认是否折叠\n    maxDepth?: number; // 初次展开最大层数\n  }>(),\n  {\n    defaultCollapsed: true, // 默认折叠\n    maxDepth: 3, // 默认展开到 3 层\n  },\n);\n\nconst isPlainObjectOrArray = computed(() => {\n  const v = props.value; // 取入参值\n  const result = v !== null && typeof v === 'object'; // 仅对象/数组才进入逐键渲染\n  logger.debug('isPlainObjectOrArray', `计算结果: ${result}。该值${result ? '是' : '不是'}普通对象或数组。`, {\n    传入值: v,\n  });\n  return result;\n}); // 是否对象/数组\n\nconst primitiveType = computed(() => {\n  const v = props.value; // 取入参值\n  let result: string;\n  if (v === null) {\n    result = 'null';\n  } else if (Array.isArray(v)) {\n    result = 'array'; // 数组（不会走到该分支，因为上面 isPlainObjectOrArray 判断过）\n  } else {\n    const t = typeof v; // 原始类型\n    result = t === 'undefined' ? 'undefined' : t;\n  }\n  logger.debug('primitiveType', `计算原始值类型: ${result}`, { 传入值: v });\n  return result;\n}); // 原始值类型名（用于着色）\n\nconst primitiveText = computed(() => {\n  const v = props.value; // 取入参值\n  let result: string;\n  switch (\n    primitiveType.value // 按类型格式化文本\n  ) {\n    case 'string':\n      result = JSON.stringify(v); // 字符串带引号\n      break;\n    case 'number':\n      result = String(v); // 数字\n      break;\n    case 'boolean':\n      result = v ? 'true' : 'false'; // 布尔\n      break;\n    case 'null':\n      result = 'null'; // null\n      break;\n    case 'undefined':\n      result = 'undefined'; // undefined\n      break;\n    case 'bigint':\n      result = String(v) + 'n'; // bigint\n      break;\n    case 'symbol':\n      result = String(v); // symbol\n      break;\n    case 'function':\n      result = 'ƒ()'; // 函数\n      break;\n    default:\n      result = ''; // 兜底\n      break;\n  }\n  logger.debug('primitiveText', `格式化原始值文本: \"${result}\"`, {\n    传入值: v,\n    类型: primitiveType.value,\n  });\n  return result;\n}); // 原始值文本\n\nconst isArrayRoot = computed(() => {\n  const result = Array.isArray(props.value);\n  logger.debug('isArrayRoot', `计算根节点是否为数组: ${result}`, { 传入值: props.value });\n  return result;\n}); // 根是否数组\nconst rootOpen = computed(() => (isArrayRoot.value ? '[' : '{')); // 根开括号\nconst rootClose = computed(() => (isArrayRoot.value ? ']' : '}')); // 根闭括号\n</script>\n\n<style scoped>\n.json-root {\n  font-size: 12px;\n  color: var(--settings-text-input);\n  transition: color 0.3s ease;\n}\n.json-line {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 2px 6px;\n  border-radius: 6px;\n  transition: background 0.2s ease;\n}\n.json-line:hover {\n  background: var(--json-hover-bg);\n}\n.json-children {\n  border-left: 1px dashed var(--json-tree-line);\n  transition: border-color 0.3s ease;\n}\n\n.key {\n  color: var(--json-key-color);\n  font-weight: 700;\n  transition: color 0.3s ease;\n}\n.colon {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n.brace {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n\n.val.string {\n  color: var(--json-val-string);\n}\n.val.number {\n  color: var(--json-val-number);\n}\n.val.boolean {\n  color: var(--json-val-boolean);\n}\n.val.null {\n  color: var(--json-val-null);\n}\n.val.undefined {\n  color: var(--json-val-null);\n}\n\n/* ===[新增] 防溢出：根与子容器都裁剪绘制，避免在外部容器高度为 0 时“透出” === */\n.json-root {\n  /* JSON 根容器：自身成为一个绘制边界 */\n  overflow: clip; /* 裁剪一切溢出（比 hidden 更省性能、无滚动条） */\n  contain: layout paint; /* 隔离布局与绘制，防外部高度计算误差 */\n  min-height: 0; /* 防止 flex 场景 min-content 顶高父级 */\n}\n\n.json-children {\n  /* 子区：同样裁剪，解决左侧虚线在收起时外露 */\n  overflow: clip; /* 子级再保险裁剪 */\n  contain: paint; /* 进一步隔离绘制 */\n}\n</style>\n"],"sourceRoot":""}]);
