`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/components/FloatingBall.vue"],"names":[],"mappings":"AAuBA,gCACE,WAAY,CACZ,yBAA2B,CAC3B,0BAA4B,CAC5B,yBAA2B,CAC3B,+CAAgD,CAChD,mCAAoC,CACpC,yCAA0C,CAC1C,sDAA2D,CAC3D,oDAAyD,CACzD,8DAAmE,CACnE,4DAAiE,CACjE,sDAA2D,CAC3D,iBAAkB,CAClB,kBAAmB,CACnB,iBAAkB,CAClB,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,cAAe,CACf,wBAAiB,CAAjB,gBAAiB,CACjB,uCAAwC,CACxC,+RAQ0G,CAC1G,mGAG0C,CAC1C,uEAGmB,CACnB,uCAA0C,CAC1C,qDACF,CAGA,uCACE,UAAW,CACX,iBAAkB,CAClB,UAAW,CACX,iBAAkB,CAClB,4FAMC,CACD,gBAAiB,CACjB,YAAa,CACb,mBAAoB,CACpB,gFAA2E,CAA3E,wEAA2E,CAC3E,gDACF,CAGA,wCACE,UAAW,CACX,iBAAkB,CAClB,SAAU,CACV,iBAAkB,CAClB,oLAE+E,CAC/E,qBAAsB,CACtB,mBAAoB,CACpB,kDAGF,CAGA,sCACE,sCAAuC,CACvC,wGAGgD,CAChD,qBACF,CAGA,8CACE,YAAa,CACb,wCACF,CAGA,uCACE,mCAAoC,CACpC,wGAIF,CAGA,8CACE,kEAAuE,CACvE,kBACF,CAGA,uCACE,gCACE,cACF,CACA,uCACE,cACF,CACF,CAGA,6BACE,QAEE,uBACF,CACA,IACE,0BACF,CACF,CAGA,8BACE,GACE,sBACF,CACA,KACE,wBACF,CACF,CAGA,4BACE,GACE,sCAAuC,CACvC,WACF,CACA,IACE,sCAAuC,CACvC,SACF,CACA,KACE,sCAAuC,CACvC,YACF,CACF,CAGA,gCACE,iBACF,CAEA,2BACE,eAAgB,CAChB,+CAAgD,CAChD,qBAAsB,CACtB,aAAc,CACd,0BAA2B,CAC3B,kBAAmB,CACnB,wBAAiB,CAAjB,gBAAiB,CACjB,mBAAoB,CACpB,SAAU,CAGV,4NAUC,CACD,4BAA6B,CAC7B,oBAAqB,CACrB,iBAAkB,CAClB,0LAG2E,CAC3E,+EAAoF,CACpF,oDAA4C,CAC5C,iDAGF,CAEA,gDACE,2LAGkE,CAClE,+EACF,CAGA,8BACE,QAEE,+BAGF,CACA,IACE,gCAGF,CACF","sourcesContent":["<template>\n  <div class=\"floating-ball\" @click=\"$emit('click')\">\n    <span class=\"era-logo\" aria-hidden=\"true\">ERA</span>\n    <!-- 仅显示用；不拦截事件 -->\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { onMounted } from 'vue';\nimport { Logger } from '../../utils/log';\n\nconst logger = new Logger();\ndefineEmits(['click']);\n\nonMounted(() => {\n  logger.log('onMounted', 'FloatingBall.vue 组件已挂载');\n});\n</script>\n\n<style scoped>\n/* =========================\n   可调外观变量（局部作用域）\n   ========================= */\n.floating-ball {\n  --size: 50px; /* 直径：保持默认 50px，与原始一致 */\n  --hue: var(--ball-hue, 212); /* 主色相：212≈#007bff，方便全局协调色相 */\n  --sat: var(--ball-sat, 100%); /* 饱和度：更鲜亮 */\n  --lum: var(--ball-lum, 52%); /* 明度：主色的明度 */\n  --surface: hsl(var(--hue) var(--sat) var(--lum)); /* 主表面色 */\n  --surface-2: hsl(var(--hue) 95% 64%); /* 渐变第二色，略亮 */\n  --ring: hsl(calc(var(--hue) + 20) 95% 65%); /* 外环的点缀色，偏青一点 */\n  --shadow: var(--ball-shadow-color, rgba(0, 123, 255, 0.36)); /* 阴影颜色（与主色相近） */\n  --glow: var(--ball-glow-color, rgba(102, 200, 255, 0.55)); /* 外发光颜色 */\n  --inner-shadow: var(--ball-inner-shadow-color, rgba(0, 0, 0, 0.22)); /* 内阴影增强体积感 */\n  --specular: var(--ball-specular-color, rgba(255, 255, 255, 0.75)); /* 高光 */\n  --glass: var(--ball-glass-color, rgba(255, 255, 255, 0.18)); /* 玻璃质感覆盖层 */\n  width: var(--size); /* 宽度设为可变量 */\n  height: var(--size); /* 高度设为可变量 */\n  border-radius: 50%; /* 圆形 */\n  position: relative; /* 作为伪元素定位参照 */\n  display: grid; /* 用 grid 实现精确居中 */\n  place-items: center; /* 居中对齐 */\n  cursor: pointer; /* 手型光标（仅视觉，不改逻辑） */\n  user-select: none; /* 防止误选中文本 */\n  -webkit-tap-highlight-color: transparent; /* 移动端点击高亮去除 */\n  background:\n    radial-gradient(\n      120% 120% at 30% 28%,\n      rgba(255, 255, 255, 0.85) 0%,\n      rgba(255, 255, 255, 0.22) 18%,\n      rgba(255, 255, 255, 0) 36%\n    ),\n    /* 玻璃高光 */ radial-gradient(120% 120% at 70% 72%, rgba(0, 0, 0, 0.18) 0%, rgba(0, 0, 0, 0) 38%),\n    /* 微内阴影，增加体积 */ conic-gradient(from 210deg at 50% 50%, var(--surface), var(--surface-2), var(--surface)); /* 主表面渐变：柔和流动感 */\n  box-shadow:\n    inset 0 2px 6px var(--inner-shadow),\n    /* 内阴影：球体边缘收束 */ 0 2px 4px rgba(0, 0, 0, 0.08),\n    /* 基础投影：贴地感 */ 0 10px 22px var(--shadow); /* 远投影：悬浮感 */\n  transition:\n    transform 0.28s ease,\n    box-shadow 0.28s ease,\n    filter 0.28s ease; /* 悬停时的平滑过渡 */\n  will-change: transform, box-shadow, filter; /* 提示浏览器优化 */\n  animation: ball-bob 3.2s ease-in-out infinite; /* 轻微上下浮动 */\n}\n\n/* 光晕外环（纯视觉层），不占点击区域 */\n.floating-ball::after {\n  content: ''; /* 生成外环层 */\n  position: absolute; /* 绝对定位贴合父元素 */\n  inset: -8px; /* 向外延展 8px，制造发光边缘 */\n  border-radius: 50%; /* 保持圆形 */\n  background: conic-gradient(\n    from 0deg,\n    var(--ring),\n    transparent 30%,\n    transparent 70%,\n    var(--ring)\n  ); /* 细腻的色环闪烁 */\n  filter: blur(6px); /* 模糊成柔和光晕 */\n  opacity: 0.55; /* 半透明，避免喧宾 */\n  pointer-events: none; /* 不拦截鼠标事件 */\n  mask: radial-gradient(farthest-side, transparent calc(100% - 12px), #000 0); /* 掏空中心形成环 */\n  animation: ring-spin 12s linear infinite; /* 慢速旋转，低侵扰 */\n}\n\n/* 玻璃高光层（更立体） */\n.floating-ball::before {\n  content: ''; /* 生成高光层 */\n  position: absolute; /* 绝对定位 */\n  inset: 2px; /* 缩进一点，保留边缘 */\n  border-radius: 50%; /* 圆形 */\n  background:\n    radial-gradient(120% 80% at 26% 24%, var(--specular) 0%, rgba(255, 255, 255, 0.2) 26%, rgba(255, 255, 255, 0) 46%),\n    /* 亮斑 */ linear-gradient(160deg, var(--glass) 0%, rgba(255, 255, 255, 0) 50%); /* 玻璃蒙层 */\n  mix-blend-mode: screen; /* 与底色叠加更自然 */\n  pointer-events: none; /* 不拦截事件 */\n  transition:\n    opacity 0.28s ease,\n    transform 0.28s ease; /* 悬停联动 */\n}\n\n/* 悬停态：轻微抬升+增强光影 */\n.floating-ball:hover {\n  transform: translateY(-2px) scale(1.04); /* 轻微升起并放大 */\n  box-shadow:\n    inset 0 2px 8px rgba(0, 0, 0, 0.22),\n    /* 内阴影略加强 */ 0 4px 10px rgba(0, 0, 0, 0.12),\n    /* 近影增强 */ 0 16px 36px rgba(0, 123, 255, 0.45); /* 远影更亮，呈现发光 */\n  filter: saturate(1.08); /* 提升饱和度一点点 */\n}\n\n/* 悬停时的微光扫过效果 */\n.floating-ball:hover::before {\n  opacity: 0.95; /* 高光更显著 */\n  animation: shimmer 1.2s ease-out; /* 一次性高光掠过 */\n}\n\n/* 按下态：略压低 */\n.floating-ball:active {\n  transform: translateY(0) scale(0.98); /* 回落一点并轻微压扁 */\n  box-shadow:\n    inset 0 2px 10px rgba(0, 0, 0, 0.28),\n    0 2px 6px rgba(0, 0, 0, 0.12),\n    0 10px 22px rgba(0, 123, 255, 0.35); /* 回到接近初始的阴影 */\n}\n\n/* 可达性：键盘聚焦时的可见描边（不改变布局） */\n.floating-ball:focus-visible {\n  outline: 2px solid var(--ball-focus-outline, rgba(102, 200, 255, 0.85)); /* 高亮描边 */\n  outline-offset: 2px; /* 外移 2px，避免遮挡球体 */\n}\n\n/* 减少动态偏好：关闭动画保持静态优雅 */\n@media (prefers-reduced-motion: reduce) {\n  .floating-ball {\n    animation: none;\n  } /* 去除上下浮动 */\n  .floating-ball::after {\n    animation: none;\n  } /* 停止外环旋转 */\n}\n\n/* 轻微上下浮动（循环） */\n@keyframes ball-bob {\n  0%,\n  100% {\n    transform: translateY(0);\n  } /* 起点/终点：原位 */\n  50% {\n    transform: translateY(-2px);\n  } /* 中点：上浮 2px */\n}\n\n/* 外环慢速旋转（不刺眼） */\n@keyframes ring-spin {\n  0% {\n    transform: rotate(0deg);\n  } /* 起点：0 度 */\n  100% {\n    transform: rotate(360deg);\n  } /* 终点：整周旋转 */\n}\n\n/* 悬停时的高光扫过 */\n@keyframes shimmer {\n  0% {\n    transform: translateY(-1px) scale(0.98);\n    opacity: 0.6;\n  } /* 初始：弱一点的高光 */\n  50% {\n    transform: translateY(-2px) scale(1.02);\n    opacity: 1;\n  } /* 中段：最亮最大 */\n  100% {\n    transform: translateY(-1px) scale(0.99);\n    opacity: 0.85;\n  } /* 结束：回落 */\n}\n\n/* ======= ERA 内标（仅视觉层） ======= */\n.floating-ball {\n  --logo-scale: 0.38;\n} /* 可调：字体占球直径比例 */\n\n.era-logo {\n  font-weight: 800; /* 粗体，增强刻感 */\n  font-size: calc(var(--size) * var(--logo-scale)); /* 跟随球大小缩放 */\n  letter-spacing: 0.02em; /* 轻微字距，避免糊 */\n  line-height: 1; /* 紧凑对齐 */\n  transform: translateY(-1px); /* 视觉微调居中 */\n  white-space: nowrap; /* 防止换行 */\n  user-select: none;\n  pointer-events: none; /* 不抢事件、不选中 */\n  z-index: 1; /* 位于背景之上（仍在 ::after 光环之下） */\n\n  /* 渐变镂空字 + 立体阴影 */\n  background: var(\n    --ball-logo-bg,\n    linear-gradient(\n      180deg,\n      rgba(255, 255, 255, 0.96) 0%,\n      rgba(255, 255, 255, 0.55) 45%,\n      rgba(220, 240, 255, 0.4) 65%,\n      rgba(120, 195, 255, 0.55) 100%\n    ),\n    linear-gradient(180deg, rgba(0, 0, 0, 0.38), rgba(0, 0, 0, 0) 60%)\n  );\n  -webkit-background-clip: text;\n  background-clip: text; /* 渐变裁剪到文字 */\n  color: transparent; /* 镂空填充由背景提供 */\n  text-shadow:\n    0 1px 0 var(--ball-logo-shadow-top, rgba(255, 255, 255, 0.75)),\n    /* 顶部掣光 */ 0 2px 4px var(--ball-logo-shadow-near, rgba(0, 0, 0, 0.25)),\n    /* 近阴影 */ 0 8px 18px var(--ball-logo-shadow-far, rgba(0, 123, 255, 0.35)); /* 远发光阴影 */\n  filter: var(--ball-logo-filter-glow, drop-shadow(0 0 2px rgba(102, 200, 255, 0.25))); /* 柔光晕 */\n  animation: era-sheen 4s ease-in-out infinite; /* 轻微高光流动 */\n  transition:\n    text-shadow 0.3s ease,\n    filter 0.3s ease;\n}\n\n.floating-ball:hover .era-logo {\n  text-shadow:\n    0 1px 0 var(--ball-logo-shadow-top, rgba(255, 255, 255, 0.85)),\n    0 3px 8px var(--ball-logo-shadow-near, rgba(0, 0, 0, 0.28)),\n    0 10px 26px var(--ball-logo-shadow-far, rgba(0, 123, 255, 0.55)); /* 悬停增强立体感 */\n  filter: var(--ball-logo-filter-glow, drop-shadow(0 0 3px rgba(102, 200, 255, 0.38))); /* 发光略加强 */\n}\n\n/* 高光缓慢上移，制造玻璃流光感（低侵扰） */\n@keyframes era-sheen {\n  0%,\n  100% {\n    background-position:\n      0% 0%,\n      0% 0%;\n  }\n  50% {\n    background-position:\n      0% 40%,\n      0% 0%;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
