`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/template/EraAccordion.vue"],"names":[],"mappings":"AAkCA,sBACE,mCAAoC,CACpC,kBAAmB,CACnB,8BAA+B,CAC/B,eAAgB,CAChB,sDAGF,CAEA,2BACE,UAAW,CACX,YAAa,CACb,kBAAmB,CACnB,QAAS,CACT,iCAAkC,CAClC,0CAA2C,CAC3C,iBAAkB,CAClB,cAAe,CACf,eAAgB,CAChB,0BAA2B,CAC3B,sEAIF,CACA,wBACE,+BACF,CACA,6BACE,uBACF,CACA,wBACE,cACF,CACA,yBACE,MACF,CACA,uBACE,cAAe,CACf,wBAAyB,CACzB,0BACF,CAEA,2BACE,YAAa,CACb,eAAgB,CAChB,uCAAwC,CACxC,mCACF,CACA,gCACE,gBAAiB,CACjB,eACF,CAEA,wBACE,iBACF","sourcesContent":["<template>\n  <section class=\"acc\">\n    <!-- 折叠头：点击切换 -->\n    <button class=\"acc-head\" :aria-expanded=\"open ? 'true' : 'false'\" @click=\"open = !open\">\n      <span class=\"caret\" :class=\"{ open }\">▸</span>\n      <!-- 指示箭头 -->\n      <span class=\"title\">{{ title }}</span>\n      <!-- 标题文本 -->\n      <span class=\"spacer\"></span>\n      <!-- 弹性空隙 -->\n      <span class=\"hint\">{{ open ? '收起' : '展开' }}</span>\n      <!-- 右侧提示 -->\n    </button>\n\n    <!-- 内容：高度过渡（使用 CSS Grid） -->\n    <div class=\"acc-body\" :class=\"{ open }\">\n      <div class=\"inner\">\n        <slot />\n      </div>\n    </div>\n  </section>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\n\nconst props = withDefaults(defineProps<{ title: string; defaultOpen?: boolean }>(), {\n  defaultOpen: false,\n});\n\nconst open = ref<boolean>(props.defaultOpen);\n</script>\n\n<style scoped>\n.acc {\n  border: 1px solid var(--border-soft);\n  border-radius: 12px;\n  background: var(--accordion-bg);\n  overflow: hidden;\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease;\n}\n\n.acc-head {\n  width: 100%;\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  background: var(--mk-pill-null-bg);\n  border-bottom: 1px solid var(--border-soft);\n  padding: 10px 12px;\n  cursor: pointer;\n  font-weight: 800;\n  color: var(--text-subtitle);\n  transition:\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n.caret {\n  transition: transform 0.18s ease;\n}\n.caret.open {\n  transform: rotate(90deg);\n}\n.title {\n  font-size: 13px;\n}\n.spacer {\n  flex: 1;\n}\n.hint {\n  font-size: 12px;\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n\n.acc-body {\n  max-height: 0;\n  overflow: hidden;\n  transition: max-height 0.28s ease-in-out;\n  background: var(--accordion-body-bg);\n}\n.acc-body.open {\n  max-height: 600px; /* 为展开的内容提供一个更大的最大高度 */\n  overflow-y: auto; /* 当内容超出最大高度时，显示滚动条 */\n}\n\n.inner {\n  padding: 10px 12px;\n}\n</style>\n"],"sourceRoot":""}]);
