`, "",{"version":3,"sources":["webpack://./src/ERA变量框架/ui/template/JsonNode.vue"],"names":[],"mappings":"AAoJA,4BACE,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,OAAQ,CACR,eAAgB,CAChB,iBAAkB,CAClB,+BACF,CACA,kCACE,+BACF,CACA,gCACE,4CAA6C,CAC7C,iCACF,CAGA,sBACE,2BAA4B,CAC5B,eAAgB,CAChB,0BACF,CACA,wBACE,wBAAyB,CACzB,0BACF,CACA,wBACE,wBAAyB,CACzB,0BACF,CACA,0BACE,wBAAyB,CACzB,eAAgB,CAChB,0BACF,CACA,6BACE,4BACF,CACA,6BACE,4BACF,CACA,8BACE,6BACF,CACA,2BACE,0BACF,CACA,gCACE,0BACF,CAGA,wBACE,UAAW,CACX,WAAY,CACZ,mCAAoC,CACpC,iBAAkB,CAClB,0BAA2B,CAC3B,gBAAiB,CACjB,iBAAkB,CAClB,cAAe,CACf,wBAAyB,CACzB,cAAe,CACf,mCAAoC,CACpC,iHAMF,CACA,6BACE,uBAAwB,CACxB,wCACF,CAGA,uBAEE,aAAc,CACd,aAAc,CACd,YACF,CAEA,gCAEE,aAAc,CACd,aACF","sourcesContent":["<template>\n  <!-- 单条节点（支持递归） -->\n  <div class=\"node\">\n    <!-- 每个键值对 / 数组项的容器 -->\n    <div class=\"json-line\" :style=\"{ paddingLeft: level * 14 + 'px' }\">\n      <!-- 行+缩进 -->\n      <button v-if=\"foldable\" class=\"caret\" :class=\"{ open }\" aria-label=\"toggle\" @click=\"open = !open\">▸</button>\n      <!-- 折叠箭头按钮 -->\n\n      <span class=\"key\">{{ k }}</span\n      ><span class=\"colon\">:</span>\n      <!-- 键与冒号 -->\n\n      <!-- 可折叠容器：只显示括号与摘要；展开后由下方 children 区域递归渲染 -->\n      <template v-if=\"foldable\">\n        <span class=\"brace\">{{ isArray ? '[' : '{' }}</span>\n        <!-- 容器起始括号 -->\n        <span v-if=\"!open\" class=\"summary\">{{ summary }}</span>\n        <!-- 收起时的摘要 -->\n      </template>\n\n      <!-- 原始值：直接着色渲染 -->\n      <template v-else>\n        <span class=\"val\" :class=\"type\">{{ primitiveText }}</span>\n        <!-- 原始值文本 -->\n      </template>\n    </div>\n\n    <!-- 子元素区域：仅当可折叠且处于展开态时显示 -->\n    <template v-if=\"foldable && open\">\n      <div class=\"json-children\">\n        <!-- 递归：自引用同名组件 JsonNode（依赖 name: 'JsonNode' 实现自递归） -->\n        <JsonNode\n          v-for=\"(childVal, childKey) in childEntries\"\n          :key=\"path + '/' + String(childKey)\"\n          :k=\"String(childKey)\"\n          :v=\"childVal\"\n          :path=\"path + '/' + String(childKey)\"\n          :level=\"level + 1\"\n          :default-collapsed=\"defaultCollapsed\"\n          :max-depth=\"maxDepth\"\n        />\n        <div class=\"json-line\" :style=\"{ paddingLeft: level * 14 + 'px' }\">\n          <span class=\"brace\">{{ isArray ? ']' : '}' }}</span>\n          <!-- 容器闭合括号 -->\n        </div>\n      </div>\n    </template>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { computed, defineComponent, onMounted, ref, watch } from 'vue'; // 引入响应式/组件工具\nimport { Logger } from '../../utils/log';\n\nexport default defineComponent({\n  name: 'JsonNode', // 关键：为自递归提供组件名\n  props: {\n    k: { type: [String, Number], required: true }, // 当前键（或数组下标）\n    v: { required: true }, // 当前值\n    path: { type: String, required: true }, // 路径（仅用于 key/调试）\n    level: { type: Number, required: true }, // 层级（用于缩进）\n    defaultCollapsed: { type: Boolean, default: true }, // 默认是否折叠\n    maxDepth: { type: Number, default: 3 }, // 初次展开的最大深度\n  },\n  setup(p) {\n    const logger = new Logger();\n\n    onMounted(() => {\n      logger.debug('onMounted', '组件已挂载', { props: p });\n    });\n\n    // 节点类型判定\n    const type = computed(() => {\n      const val = p.v;\n      if (val === null) return 'null';\n      if (Array.isArray(val)) return 'array';\n      const t = typeof val;\n      if (t === 'object') return 'object';\n      if (t === 'undefined') return 'undefined';\n      return t as 'string' | 'number' | 'boolean' | 'bigint' | 'symbol' | 'function';\n    });\n\n    // 是否数组/对象\n    const isArray = computed(() => type.value === 'array');\n    const isObject = computed(() => type.value === 'object');\n\n    // 是否可折叠\n    const foldable = computed(() => isArray.value || isObject.value);\n\n    // 初始开合：不超过 maxDepth 的层级默认展开；否则遵循 defaultCollapsed\n    const open = ref<boolean>(p.level <= (p.maxDepth ?? 3) ? true : !p.defaultCollapsed);\n\n    watch(open, newOpenState => {\n      logger.debug('watch:open', `折叠状态改变为: ${newOpenState ? '展开' : '收起'}`);\n    });\n\n    // 原始值渲染文本（补足 undefined / bigint / symbol）\n    const primitiveText = computed(() => {\n      const val = p.v as any;\n      switch (type.value) {\n        case 'string':\n          return JSON.stringify(val);\n        case 'number':\n          return String(val);\n        case 'boolean':\n          return val ? 'true' : 'false';\n        case 'null':\n          return 'null';\n        case 'undefined':\n          return 'undefined';\n        case 'bigint':\n          return String(val) + 'n';\n        case 'symbol':\n          return String(val);\n        case 'function':\n          return 'ƒ()';\n        default:\n          return '';\n      }\n    });\n\n    // 收起时的摘要信息\n    const summary = computed(() => {\n      if (isArray.value) return `Array[${(p.v as any[]).length}]`;\n      if (isObject.value) return `Object{${Object.keys(p.v || {}).length}}`;\n      return '';\n    });\n\n    // 子项列表：对象 → 其 entries；数组 → 直接枚举索引和值\n    const childEntries = computed<Record<string, any>>(() => {\n      if (isObject.value) return p.v as Record<string, any>;\n      if (isArray.value) {\n        const out: Record<string, any> = {};\n        (p.v as any[]).forEach((cv, idx) => {\n          out[String(idx)] = cv;\n        });\n        return out;\n      }\n      return {};\n    });\n\n    return { type, isArray, foldable, open, primitiveText, summary, childEntries };\n  },\n});\n</script>\n\n<style scoped>\n.json-line {\n  position: relative;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  padding: 2px 6px;\n  border-radius: 6px;\n  transition: background 0.2s ease;\n}\n.json-line:hover {\n  background: var(--json-hover-bg);\n}\n.json-children {\n  border-left: 1px dashed var(--json-tree-line);\n  transition: border-color 0.3s ease;\n}\n\n/* 结构元素与着色 */\n.key {\n  color: var(--json-key-color);\n  font-weight: 700;\n  transition: color 0.3s ease;\n}\n.colon {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n.brace {\n  color: var(--text-normal);\n  transition: color 0.3s ease;\n}\n.summary {\n  color: var(--text-normal);\n  margin-left: 4px;\n  transition: color 0.3s ease;\n}\n.val.string {\n  color: var(--json-val-string);\n}\n.val.number {\n  color: var(--json-val-number);\n}\n.val.boolean {\n  color: var(--json-val-boolean);\n}\n.val.null {\n  color: var(--json-val-null);\n}\n.val.undefined {\n  color: var(--json-val-null);\n}\n\n/* 折叠箭头 */\n.caret {\n  width: 18px;\n  height: 18px;\n  border: 1px solid var(--border-soft);\n  border-radius: 4px;\n  background: var(--bg-solid);\n  line-height: 16px;\n  text-align: center;\n  font-size: 10px;\n  color: var(--text-normal);\n  cursor: pointer;\n  box-shadow: var(--json-caret-shadow);\n  transition:\n    transform 0.18s ease,\n    box-shadow 0.18s ease,\n    background 0.3s ease,\n    border-color 0.3s ease,\n    color 0.3s ease;\n}\n.caret.open {\n  transform: rotate(90deg);\n  box-shadow: var(--json-caret-open-shadow);\n}\n\n/* ===[新增] 防溢出：节点与子树都裁剪（配合上层折叠时彻底看不见） === */\n.node {\n  /* 单个节点容器 */\n  overflow: clip; /* 裁剪节点内部所有绘制 */\n  contain: paint; /* 隔离绘制，避免 1px 渲染外泄 */\n  min-height: 0; /* 防“最小高度”把父级撑开 */\n}\n\n.json-children {\n  /* 子节点区域 */\n  overflow: clip; /* 裁剪子树（含左侧虚线等装饰） */\n  contain: paint; /* 防止子元素阴影/边框超界 */\n}\n</style>\n"],"sourceRoot":""}]);
